export enum ScenarioType {
  TRANSIT = 'TRANSIT',
  CAMPUS = 'CAMPUS',
  RETAIL = 'RETAIL',
  UNIVERSITY = 'UNIVERSITY',
}

export enum FareStrategyType {
  FLAT_RATE = 'FLAT_RATE',
  DISTANCE = 'DISTANCE',
  TIME = 'TIME',
}

export enum TransferDiscountType {
  FIXED_AMOUNT = 'FIXED_AMOUNT',
  FREE = 'FREE',
  NONE = 'NONE',
}

export enum SubsidyResetPolicy {
  MONTHLY_RESET = 'MONTHLY_RESET',
  ACCUMULATE = 'ACCUMULATE',
  NEVER = 'NEVER',
}

export enum BirthdayBenefit {
  DOUBLE_POINTS = 'DOUBLE_POINTS',
  GIFT_COUPON = 'GIFT_COUPON',
}

export interface MemberTierRule {
  tier: string;
  threshold: number;
  discount: number;
}

export interface RechargeBonusRule {
  min: number;
  gift: number;
}

export interface ScenarioConfig {
  configId: string;
  scenarioCode: ScenarioType;
  configName: string;
  isActive: boolean;
  effectiveTime: string;
  expireTime?: string;
  cardTypeMapping?: string[];
  currencyUnit: string;
  createdAt: string;
  updatedAt: string;
  createdBy?: string;

  fareStrategyType?: FareStrategyType;
  baseFare?: number;
  maxFareCap?: number;
  transferWindowMin?: number;
  transferDiscountType?: TransferDiscountType;
  transferDiscountValue?: number;
  overdraftAllowed: boolean;
  overdraftLimit?: number;
  offlineUploadThreshold?: number;
  blacklistVersion?: string;
  checkinCheckoutRequired: boolean;

  walletPriority?: string[];
  subsidyResetPolicy?: SubsidyResetPolicy;
  subsidyResetDay?: number;
  autoSubsidyAmount?: number;
  allowCashWithdraw: boolean;
  attendanceLinkage: boolean;
  allowedZones?: string[];
  mealTimeRestrictions?: Record<string, string>;
  visitorEscortRequired: boolean;
  departmentCostCenter?: string;

  pointsEarnRate?: number;
  pointsBurnRate?: number;
  pointsValidityDays?: number;
  memberTierRules?: MemberTierRule[];
  rechargeBonusRules?: RechargeBonusRule[];
  birthdayBenefit?: BirthdayBenefit;
  allowNegativePoints: boolean;
  promotionExcludeSkus?: string[];
  familyAccountLink: boolean;

  waterPulsePrice?: number;
  powerKwhPrice?: number;
  showerPreDeduct?: number;
  libraryDebtThreshold?: number;
  dormCurfewStart?: string;
  dormCurfewEnd?: string;
  identityExpiryDate?: string;
  alumniConversion: boolean;
  campusBusFare?: number;
  netAuthLinkage: boolean;
}

export interface CreateScenarioConfigDto {
  scenarioCode: ScenarioType;
  configName: string;
  isActive?: boolean;
  effectiveTime: string;
  expireTime?: string;
  cardTypeMapping?: string[];
  currencyUnit?: string;

  fareStrategyType?: FareStrategyType;
  baseFare?: number;
  maxFareCap?: number;
  transferWindowMin?: number;
  transferDiscountType?: TransferDiscountType;
  transferDiscountValue?: number;
  overdraftAllowed?: boolean;
  overdraftLimit?: number;
  offlineUploadThreshold?: number;
  blacklistVersion?: string;
  checkinCheckoutRequired?: boolean;

  walletPriority?: string[];
  subsidyResetPolicy?: SubsidyResetPolicy;
  subsidyResetDay?: number;
  autoSubsidyAmount?: number;
  allowCashWithdraw?: boolean;
  attendanceLinkage?: boolean;
  allowedZones?: string[];
  mealTimeRestrictions?: Record<string, string>;
  visitorEscortRequired?: boolean;
  departmentCostCenter?: string;

  pointsEarnRate?: number;
  pointsBurnRate?: number;
  pointsValidityDays?: number;
  memberTierRules?: MemberTierRule[];
  rechargeBonusRules?: RechargeBonusRule[];
  birthdayBenefit?: BirthdayBenefit;
  allowNegativePoints?: boolean;
  promotionExcludeSkus?: string[];
  familyAccountLink?: boolean;

  waterPulsePrice?: number;
  powerKwhPrice?: number;
  showerPreDeduct?: number;
  libraryDebtThreshold?: number;
  dormCurfewStart?: string;
  dormCurfewEnd?: string;
  identityExpiryDate?: string;
  alumniConversion?: boolean;
  campusBusFare?: number;
  netAuthLinkage?: boolean;
}

export interface UpdateScenarioConfigDto extends CreateScenarioConfigDto {}

