import { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { useTranslation } from 'react-i18next';
import { X, Save } from 'lucide-react';
import {
  ScenarioType,
  scenarioTypeTranslationKeys,
  FareStrategyType,
  TransferDiscountType,
  SubsidyResetPolicy,
  BirthdayBenefit,
  CreateScenarioConfigDto,
} from '../../types/scenario-config';
import { useCreateScenarioConfig, useActiveScenarioConfig } from '../../hooks/useScenarioConfig';
import { showSuccessToast, showErrorToast } from '../ui/Toast';

interface Props {
  isOpen: boolean;
  onClose: () => void;
  scenario: ScenarioType | null;
}

export default function ScenarioConfigDrawer({ isOpen, onClose, scenario }: Props) {
  const { t } = useTranslation();
  const { data: activeConfig } = useActiveScenarioConfig(scenario || ScenarioType.TRANSIT);
  const createMutation = useCreateScenarioConfig();

  const [formData, setFormData] = useState<Partial<CreateScenarioConfigDto>>({
    configName: '',
    isActive: true,
    effectiveTime: new Date().toISOString().split('T')[0],
    currencyUnit: 'CNY',
  });

  useEffect(() => {
    if (activeConfig) {
      setFormData({
        configName: activeConfig.configName,
        isActive: activeConfig.isActive,
        effectiveTime: activeConfig.effectiveTime.split('T')[0],
        expireTime: activeConfig.expireTime?.split('T')[0],
        currencyUnit: activeConfig.currencyUnit,
        fareStrategyType: activeConfig.fareStrategyType,
        baseFare: activeConfig.baseFare,
        maxFareCap: activeConfig.maxFareCap,
        transferWindowMin: activeConfig.transferWindowMin,
        transferDiscountType: activeConfig.transferDiscountType,
        transferDiscountValue: activeConfig.transferDiscountValue,
        overdraftAllowed: activeConfig.overdraftAllowed,
        overdraftLimit: activeConfig.overdraftLimit,
        offlineUploadThreshold: activeConfig.offlineUploadThreshold,
        blacklistVersion: activeConfig.blacklistVersion,
        checkinCheckoutRequired: activeConfig.checkinCheckoutRequired,
        walletPriority: activeConfig.walletPriority,
        subsidyResetPolicy: activeConfig.subsidyResetPolicy,
        attendanceLinkage: activeConfig.attendanceLinkage,
        pointsEarnRate: activeConfig.pointsEarnRate,
        pointsBurnRate: activeConfig.pointsBurnRate,
        birthdayBenefit: activeConfig.birthdayBenefit,
        familyAccountLink: activeConfig.familyAccountLink,
        waterPulsePrice: activeConfig.waterPulsePrice,
        powerKwhPrice: activeConfig.powerKwhPrice,
        libraryDebtThreshold: activeConfig.libraryDebtThreshold,
        dormCurfewStart: activeConfig.dormCurfewStart,
        dormCurfewEnd: activeConfig.dormCurfewEnd,
        netAuthLinkage: activeConfig.netAuthLinkage,
      });
    } else if (scenario) {
      setFormData({
        configName: `${t(scenarioTypeTranslationKeys[scenario])} ${t('scenarioConfig.defaultConfigName')}`,
        isActive: true,
        effectiveTime: new Date().toISOString().split('T')[0],
        currencyUnit: 'CNY',
        overdraftAllowed: false,
        checkinCheckoutRequired: false,
      });
    }
  }, [activeConfig, scenario, t]);

  const handleSubmit = async () => {
    if (!scenario || !formData.configName) return;

    try {
      await createMutation.mutateAsync({
        scenarioCode: scenario,
        ...formData,
      } as CreateScenarioConfigDto);
      showSuccessToast(t('scenarioConfig.saveSuccess'));
      onClose();
    } catch (error) {
      showErrorToast(t('scenarioConfig.saveFailed'));
    }
  };

  if (!scenario) return null;

  return (
    <AnimatePresence>
      {isOpen && (
        <>
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            onClick={onClose}
            className="fixed inset-0 bg-black/50 z-40"
          />
          <motion.div
            initial={{ x: '100%' }}
            animate={{ x: 0 }}
            exit={{ x: '100%' }}
            transition={{ type: 'spring', damping: 25, stiffness: 200 }}
            className="fixed right-0 top-0 h-full w-full max-w-2xl bg-white dark:bg-slate-800 z-50 shadow-2xl overflow-hidden"
          >
            <div className="flex flex-col h-full">
              <div className="flex items-center justify-between p-6 border-b border-slate-200 dark:border-slate-700">
                <div>
                  <h2 className="text-xl font-bold text-slate-900 dark:text-slate-100">
                    {t(scenarioTypeTranslationKeys[scenario])} {t('scenarioConfig.settings')}
                  </h2>
                  <p className="text-sm text-slate-500 mt-1">
                    {activeConfig ? t('scenarioConfig.editConfig') : t('scenarioConfig.createConfig')}
                  </p>
                </div>
                <button
                  type="button"
                  onClick={onClose}
                  className="p-2 hover:bg-slate-100 dark:hover:bg-slate-700 rounded-lg transition-colors"
                >
                  <X className="w-5 h-5 text-slate-500" />
                </button>
              </div>

              <div className="flex-1 overflow-y-auto p-6 space-y-6">
                <div className="space-y-4">
                  <div>
                    <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                      {t('scenarioConfig.configName')}
                    </label>
                    <input
                      type="text"
                      value={formData.configName || ''}
                      onChange={(e) => setFormData({ ...formData, configName: e.target.value })}
                      className="w-full px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-slate-700 dark:text-slate-100"
                      placeholder={t('scenarioConfig.enterName')}
                    />
                  </div>

                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                        {t('scenarioConfig.effectiveTime')}
                      </label>
                      <input
                        type="date"
                        value={formData.effectiveTime || ''}
                        onChange={(e) => setFormData({ ...formData, effectiveTime: e.target.value })}
                        className="w-full px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-slate-700 dark:text-slate-100"
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                        {t('scenarioConfig.expireTime')}
                      </label>
                      <input
                        type="date"
                        value={formData.expireTime || ''}
                        onChange={(e) => setFormData({ ...formData, expireTime: e.target.value })}
                        className="w-full px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-slate-700 dark:text-slate-100"
                      />
                    </div>
                  </div>

                  {/* 公共交通场景 */}
                  {scenario === ScenarioType.TRANSIT && (
                    <div className="space-y-4 border-t border-slate-200 dark:border-slate-700 pt-4">
                      <h3 className="font-semibold text-slate-900 dark:text-slate-100">
                        {t('scenarioConfig.transitSettings')}
                      </h3>

                      <div>
                        <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                          {t('scenarioConfig.transit.fareStrategy')}
                        </label>
                        <select
                          value={formData.fareStrategyType || ''}
                          onChange={(e) => setFormData({ ...formData, fareStrategyType: e.target.value as FareStrategyType })}
                          className="w-full px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-slate-700 dark:text-slate-100"
                        >
                          <option value="">请选择</option>
                          <option value={FareStrategyType.FLAT_RATE}>{t('scenarioConfig.transit.flatRate')}</option>
                          <option value={FareStrategyType.DISTANCE}>{t('scenarioConfig.transit.distanceBased')}</option>
                          <option value={FareStrategyType.TIME}>{t('scenarioConfig.transit.timeBased')}</option>
                        </select>
                      </div>

                      <div className="grid grid-cols-2 gap-4">
                        <div>
                          <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                            {t('scenarioConfig.transit.baseFare')}
                          </label>
                          <input
                            type="number"
                            step="0.01"
                            value={formData.baseFare || ''}
                            onChange={(e) => setFormData({ ...formData, baseFare: parseFloat(e.target.value) })}
                            className="w-full px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-slate-700 dark:text-slate-100"
                            placeholder="2.00"
                          />
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                            {t('scenarioConfig.transit.maxFareCap')}
                          </label>
                          <input
                            type="number"
                            step="0.01"
                            value={formData.maxFareCap || ''}
                            onChange={(e) => setFormData({ ...formData, maxFareCap: parseFloat(e.target.value) })}
                            className="w-full px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-slate-700 dark:text-slate-100"
                            placeholder="10.00"
                          />
                        </div>
                      </div>

                      <div className="grid grid-cols-2 gap-4">
                        <div>
                          <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                            {t('scenarioConfig.transit.transferWindow')}
                          </label>
                          <input
                            type="number"
                            value={formData.transferWindowMin || ''}
                            onChange={(e) => setFormData({ ...formData, transferWindowMin: parseInt(e.target.value) })}
                            className="w-full px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-slate-700 dark:text-slate-100"
                            placeholder="30"
                          />
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                            {t('scenarioConfig.transit.transferDiscount')}
                          </label>
                          <select
                            value={formData.transferDiscountType || ''}
                            onChange={(e) => setFormData({ ...formData, transferDiscountType: e.target.value as TransferDiscountType })}
                            className="w-full px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-slate-700 dark:text-slate-100"
                          >
                            <option value="">请选择</option>
                            <option value={TransferDiscountType.NONE}>{t('scenarioConfig.transferDiscount.none')}</option>
                            <option value={TransferDiscountType.FIXED_AMOUNT}>{t('scenarioConfig.transferDiscount.fixed')}</option>
                            <option value={TransferDiscountType.FREE}>{t('scenarioConfig.transferDiscount.percentage')}</option>
                          </select>
                        </div>
                      </div>

                      {(formData.transferDiscountType === TransferDiscountType.FIXED_AMOUNT || formData.transferDiscountType === TransferDiscountType.FREE) && (
                        <div>
                          <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                            {t('scenarioConfig.transit.transferDiscountValue')}
                          </label>
                          <input
                            type="number"
                            step="0.01"
                            value={formData.transferDiscountValue || ''}
                            onChange={(e) => setFormData({ ...formData, transferDiscountValue: parseFloat(e.target.value) })}
                            className="w-full px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-slate-700 dark:text-slate-100"
                            placeholder={formData.transferDiscountType === TransferDiscountType.FIXED_AMOUNT ? "0.50" : "20"}
                          />
                        </div>
                      )}

                      <div className="border-t border-slate-200 dark:border-slate-700 pt-4 mt-4">
                        <div className="flex items-center gap-2 mb-4">
                          <input
                            type="checkbox"
                            id="overdraftAllowed"
                            checked={formData.overdraftAllowed || false}
                            onChange={(e) => setFormData({ ...formData, overdraftAllowed: e.target.checked })}
                            className="w-4 h-4 text-blue-600 border-slate-300 rounded focus:ring-blue-500"
                          />
                          <label htmlFor="overdraftAllowed" className="text-sm text-slate-700 dark:text-slate-300">
                            {t('scenarioConfig.transit.overdraftAllowed')}
                          </label>
                        </div>
                        {formData.overdraftAllowed && (
                          <div>
                            <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                              {t('scenarioConfig.transit.overdraftLimit')}
                            </label>
                            <input
                              type="number"
                              step="0.01"
                              value={formData.overdraftLimit || ''}
                              onChange={(e) => setFormData({ ...formData, overdraftLimit: parseFloat(e.target.value) })}
                              className="w-full px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-slate-700 dark:text-slate-100"
                              placeholder="10.00"
                            />
                          </div>
                        )}
                      </div>

                      <div className="grid grid-cols-2 gap-4">
                        <div>
                          <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                            {t('scenarioConfig.transit.offlineThreshold')}
                          </label>
                          <input
                            type="number"
                            value={formData.offlineUploadThreshold || ''}
                            onChange={(e) => setFormData({ ...formData, offlineUploadThreshold: parseInt(e.target.value) })}
                            className="w-full px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-slate-700 dark:text-slate-100"
                            placeholder="100"
                          />
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                            {t('scenarioConfig.transit.blacklistVersion')}
                          </label>
                          <input
                            type="text"
                            value={formData.blacklistVersion || ''}
                            onChange={(e) => setFormData({ ...formData, blacklistVersion: e.target.value })}
                            className="w-full px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-slate-700 dark:text-slate-100"
                            placeholder="v1.0.0"
                          />
                        </div>
                      </div>

                      <div className="flex items-center gap-2">
                        <input
                          type="checkbox"
                          id="checkinCheckoutRequired"
                          checked={formData.checkinCheckoutRequired || false}
                          onChange={(e) => setFormData({ ...formData, checkinCheckoutRequired: e.target.checked })}
                          className="w-4 h-4 text-blue-600 border-slate-300 rounded focus:ring-blue-500"
                        />
                        <label htmlFor="checkinCheckoutRequired" className="text-sm text-slate-700 dark:text-slate-300">
                          {t('scenarioConfig.transit.checkinCheckout')}
                        </label>
                      </div>
                    </div>
                  )}

                  {/* 企业园区场景 */}
                  {scenario === ScenarioType.CAMPUS && (
                    <div className="space-y-4 border-t border-slate-200 dark:border-slate-700 pt-4">
                      <h3 className="font-semibold text-slate-900 dark:text-slate-100">
                        {t('scenarioConfig.campus.title')}
                      </h3>

                      <div>
                        <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                          {t('scenarioConfig.campus.walletPriority')}
                        </label>
                        <select
                          value={formData.walletPriority?.[0] || ''}
                          onChange={(e) => setFormData({ ...formData, walletPriority: [e.target.value] })}
                          className="w-full px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-slate-700 dark:text-slate-100"
                        >
                          <option value="">请选择</option>
                          <option value="SUBSIDY">{t('paymentMethods.subsidy')}</option>
                          <option value="PERSONAL">{t('paymentMethods.cash')}</option>
                        </select>
                      </div>

                      <div>
                        <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                          {t('scenarioConfig.campus.subsidyResetPolicy')}
                        </label>
                        <select
                          value={formData.subsidyResetPolicy || ''}
                          onChange={(e) => setFormData({ ...formData, subsidyResetPolicy: e.target.value as SubsidyResetPolicy })}
                          className="w-full px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-slate-700 dark:text-slate-100"
                        >
                          <option value="">请选择</option>
                          <option value={SubsidyResetPolicy.MONTHLY_RESET}>{t('scenarioConfig.subsidyReset.monthly')}</option>
                          <option value={SubsidyResetPolicy.ACCUMULATE}>{t('scenarioConfig.subsidyReset.never')}</option>
                          <option value={SubsidyResetPolicy.NEVER}>{t('scenarioConfig.subsidyReset.never')}</option>
                        </select>
                      </div>

                      <div className="flex items-center gap-2">
                        <input
                          type="checkbox"
                          id="attendanceLinkage"
                          checked={formData.attendanceLinkage || false}
                          onChange={(e) => setFormData({ ...formData, attendanceLinkage: e.target.checked })}
                          className="w-4 h-4 text-blue-600 border-slate-300 rounded focus:ring-blue-500"
                        />
                        <label htmlFor="attendanceLinkage" className="text-sm text-slate-700 dark:text-slate-300">
                          {t('scenarioConfig.campus.attendanceLinkage')}
                        </label>
                      </div>
                    </div>
                  )}

                  {/* 零售会员场景 */}
                  {scenario === ScenarioType.RETAIL && (
                    <div className="space-y-4 border-t border-slate-200 dark:border-slate-700 pt-4">
                      <h3 className="font-semibold text-slate-900 dark:text-slate-100">
                        {t('scenarioConfig.retail.title')}
                      </h3>

                      <div className="grid grid-cols-2 gap-4">
                        <div>
                          <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                            {t('scenarioConfig.retail.pointsEarnRate')}
                          </label>
                          <input
                            type="number"
                            step="0.01"
                            value={formData.pointsEarnRate || ''}
                            onChange={(e) => setFormData({ ...formData, pointsEarnRate: parseFloat(e.target.value) })}
                            className="w-full px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-slate-700 dark:text-slate-100"
                            placeholder="1.00"
                          />
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                            {t('scenarioConfig.retail.pointsBurnRate')}
                          </label>
                          <input
                            type="number"
                            step="0.01"
                            value={formData.pointsBurnRate || ''}
                            onChange={(e) => setFormData({ ...formData, pointsBurnRate: parseFloat(e.target.value) })}
                            className="w-full px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-slate-700 dark:text-slate-100"
                            placeholder="100"
                          />
                        </div>
                      </div>

                      <div>
                        <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                          {t('scenarioConfig.retail.birthdayBenefit')}
                        </label>
                        <select
                          value={formData.birthdayBenefit || ''}
                          onChange={(e) => setFormData({ ...formData, birthdayBenefit: e.target.value as BirthdayBenefit })}
                          className="w-full px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-slate-700 dark:text-slate-100"
                        >
                          <option value="">请选择</option>
                          <option value={BirthdayBenefit.DOUBLE_POINTS}>{t('scenarioConfig.birthdayBenefit.doublePoints')}</option>
                          <option value={BirthdayBenefit.GIFT_COUPON}>{t('scenarioConfig.birthdayBenefit.discount')}</option>
                        </select>
                      </div>

                      <div className="flex items-center gap-2">
                        <input
                          type="checkbox"
                          id="familyAccountLink"
                          checked={formData.familyAccountLink || false}
                          onChange={(e) => setFormData({ ...formData, familyAccountLink: e.target.checked })}
                          className="w-4 h-4 text-blue-600 border-slate-300 rounded focus:ring-blue-500"
                        />
                        <label htmlFor="familyAccountLink" className="text-sm text-slate-700 dark:text-slate-300">
                          {t('scenarioConfig.retail.familyAccountLink')}
                        </label>
                      </div>
                    </div>
                  )}

                  {/* 大学校园场景 */}
                  {scenario === ScenarioType.UNIVERSITY && (
                    <div className="space-y-4 border-t border-slate-200 dark:border-slate-700 pt-4">
                      <h3 className="font-semibold text-slate-900 dark:text-slate-100">
                        {t('scenarioConfig.university.title')}
                      </h3>

                      <div className="grid grid-cols-2 gap-4">
                        <div>
                          <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                            {t('scenarioConfig.university.waterPulsePrice')}
                          </label>
                          <input
                            type="number"
                            step="0.01"
                            value={formData.waterPulsePrice || ''}
                            onChange={(e) => setFormData({ ...formData, waterPulsePrice: parseFloat(e.target.value) })}
                            className="w-full px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-slate-700 dark:text-slate-100"
                            placeholder="0.01"
                          />
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                            {t('scenarioConfig.university.powerKwhPrice')}
                          </label>
                          <input
                            type="number"
                            step="0.01"
                            value={formData.powerKwhPrice || ''}
                            onChange={(e) => setFormData({ ...formData, powerKwhPrice: parseFloat(e.target.value) })}
                            className="w-full px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-slate-700 dark:text-slate-100"
                            placeholder="0.60"
                          />
                        </div>
                      </div>

                      <div>
                        <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                          {t('scenarioConfig.university.libraryDebtThreshold')}
                        </label>
                        <input
                          type="number"
                          value={formData.libraryDebtThreshold || ''}
                          onChange={(e) => setFormData({ ...formData, libraryDebtThreshold: parseFloat(e.target.value) })}
                          className="w-full px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-slate-700 dark:text-slate-100"
                          placeholder="50.00"
                        />
                      </div>

                      <div className="grid grid-cols-2 gap-4">
                        <div>
                          <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                            {t('scenarioConfig.university.dormCurfewStart')}
                          </label>
                          <input
                            type="time"
                            value={formData.dormCurfewStart || ''}
                            onChange={(e) => setFormData({ ...formData, dormCurfewStart: e.target.value })}
                            className="w-full px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-slate-700 dark:text-slate-100"
                          />
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                            {t('scenarioConfig.university.dormCurfewEnd')}
                          </label>
                          <input
                            type="time"
                            value={formData.dormCurfewEnd || ''}
                            onChange={(e) => setFormData({ ...formData, dormCurfewEnd: e.target.value })}
                            className="w-full px-4 py-2 border border-slate-300 dark:border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent dark:bg-slate-700 dark:text-slate-100"
                          />
                        </div>
                      </div>

                      <div className="flex items-center gap-2">
                        <input
                          type="checkbox"
                          id="netAuthLinkage"
                          checked={formData.netAuthLinkage || false}
                          onChange={(e) => setFormData({ ...formData, netAuthLinkage: e.target.checked })}
                          className="w-4 h-4 text-blue-600 border-slate-300 rounded focus:ring-blue-500"
                        />
                        <label htmlFor="netAuthLinkage" className="text-sm text-slate-700 dark:text-slate-300">
                          {t('scenarioConfig.university.netAuthLinkage')}
                        </label>
                      </div>
                    </div>
                  )}

                  <div className="flex items-center gap-2 pt-4">
                    <input
                      type="checkbox"
                      id="isActive"
                      checked={formData.isActive}
                      onChange={(e) => setFormData({ ...formData, isActive: e.target.checked })}
                      className="w-4 h-4 text-blue-600 border-slate-300 rounded focus:ring-blue-500"
                    />
                    <label htmlFor="isActive" className="text-sm text-slate-700 dark:text-slate-300">
                      {t('scenarioConfig.isActive')}
                    </label>
                  </div>
                </div>
              </div>

              <div className="p-6 border-t border-slate-200 dark:border-slate-700 flex gap-3">
                <button
                  type="button"
                  onClick={onClose}
                  className="flex-1 px-4 py-2 border border-slate-300 dark:border-slate-600 text-slate-700 dark:text-slate-300 rounded-lg hover:bg-slate-50 dark:hover:bg-slate-700 transition-colors"
                >
                  {t('common.cancel')}
                </button>
                <button
                  type="button"
                  onClick={handleSubmit}
                  disabled={createMutation.isPending}
                  className="flex-1 flex items-center justify-center gap-2 px-4 py-2 bg-blue-600 hover:bg-blue-700 disabled:bg-blue-400 text-white rounded-lg transition-colors"
                >
                  {createMutation.isPending ? (
                    <div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                  ) : (
                    <Save className="w-4 h-4" />
                  )}
                  {t('common.save')}
                </button>
              </div>
            </div>
          </motion.div>
        </>
      )}
    </AnimatePresence>
  );
}
