import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import apiClient from '../api/client';
import {
  ScenarioConfig,
  CreateScenarioConfigDto,
  UpdateScenarioConfigDto,
  ScenarioType,
} from '../types/scenario-config';

const SCENARIO_CONFIG_KEY = 'scenarioConfigs';

export const useScenarioConfigs = (scenarioCode?: ScenarioType) => {
  return useQuery({
    queryKey: [SCENARIO_CONFIG_KEY, scenarioCode],
    queryFn: async () => {
      const params = scenarioCode ? { scenarioCode } : {};
      const response = await apiClient.get('/scenario-configs', { params });
      return response.data as ScenarioConfig[];
    },
  });
};

export const useScenarioConfig = (id: string) => {
  return useQuery({
    queryKey: [SCENARIO_CONFIG_KEY, id],
    queryFn: async () => {
      const response = await apiClient.get(`/scenario-configs/${id}`);
      return response.data as ScenarioConfig;
    },
    enabled: !!id,
  });
};

export const useActiveScenarioConfig = (scenarioCode: ScenarioType) => {
  return useQuery({
    queryKey: [SCENARIO_CONFIG_KEY, 'active', scenarioCode],
    queryFn: async () => {
      const response = await apiClient.get(`/scenario-configs/active/${scenarioCode}`);
      return response.data as ScenarioConfig | null;
    },
    enabled: !!scenarioCode,
  });
};

export const useScenarioTypes = () => {
  return useQuery({
    queryKey: [SCENARIO_CONFIG_KEY, 'types'],
    queryFn: async () => {
      const response = await apiClient.get('/scenario-configs/types');
      return response.data as string[];
    },
  });
};

export const useCreateScenarioConfig = () => {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: async (dto: CreateScenarioConfigDto) => {
      const response = await apiClient.post('/scenario-configs', dto);
      return response.data as ScenarioConfig;
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: [SCENARIO_CONFIG_KEY] });
    },
  });
};

export const useUpdateScenarioConfig = () => {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: async ({ id, dto }: { id: string; dto: UpdateScenarioConfigDto }) => {
      const response = await apiClient.put(`/scenario-configs/${id}`, dto);
      return response.data as ScenarioConfig;
    },
    onSuccess: (_, { id }) => {
      queryClient.invalidateQueries({ queryKey: [SCENARIO_CONFIG_KEY] });
      queryClient.invalidateQueries({ queryKey: [SCENARIO_CONFIG_KEY, id] });
    },
  });
};

export const useDeleteScenarioConfig = () => {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: async (id: string) => {
      await apiClient.delete(`/scenario-configs/${id}`);
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: [SCENARIO_CONFIG_KEY] });
    },
  });
};

export const useActivateScenarioConfig = () => {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: async (id: string) => {
      const response = await apiClient.post(`/scenario-configs/${id}/activate`);
      return response.data as ScenarioConfig;
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: [SCENARIO_CONFIG_KEY] });
    },
  });
};

export const useDeactivateScenarioConfig = () => {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: async (id: string) => {
      const response = await apiClient.post(`/scenario-configs/${id}/deactivate`);
      return response.data as ScenarioConfig;
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: [SCENARIO_CONFIG_KEY] });
    },
  });
};
